<template>
	<div class="pokemonCard">
		<div class="card pokemon">
		  <!-- <img class="card-img-top" src="..." alt="Card image cap"> -->
		  <div class="card-body">
		    <h4 class="card-title" >{{ pokemon | capitalizeFirstLetter }}</h4>
		    <img :src="getPokemonSprites(pokemonData.id)"/>
		    <p class="card-text"><strong>Pok&eacute;dex #</strong> : {{ pokemonData.id }} </p>
		    <ul class="list-group list-group-flush">
			   <li class="list-group-item"><strong>Speed</strong> : {{ pokemonData.speed }}</li>
			   <li class="list-group-item"><strong>Special Defense</strong> : {{ pokemonData.specialDefense }}</li>
			   <li class="list-group-item"><strong>Special Attack</strong> : {{ pokemonData.specialAttack }}</li>
			   <li class="list-group-item"><strong>Defense</strong> : {{ pokemonData.defense }}</li>
			   <li class="list-group-item"><strong>Attack</strong> : {{ pokemonData.attack }}</li>
			   <li class="list-group-item"><strong>HP</strong> : {{ pokemonData.hp }}</li>
			</ul>
		  </div>
		</div>
	</div>

</template>


<script>
	import Vue from 'vue'
	import axios from 'axios'
	import VueAxios from 'vue-axios'
	
	Vue.use(VueAxios, axios);
	const pathToCats = require.context('../../assets/sprites', true);

	export default{
		props : {
			pokemon : String
		},
		data () {
			return {
				PokeApiURL : 'https://pokeapi.co/api/v2/',
				mainPokeApiPage: 'https://pokeapi.co',
				pokemonData : {
					allInfo : '' ,
					speed : '',
					specialDefense : '',
					specialAttack : '',
					defense : '',
					attack : '',
					hp : '',
					id : ''
				},
				pokemonSprite : require('../../assets/sprites/0.png')

			};
		},
		filters : {
			capitalizeFirstLetter(value){
				if (!value) return ''
  				value = value.toString()
  				return value.charAt(0).toUpperCase() + value.slice(1)
			}
		},
		mounted () {

			// gets pokemon info
		    axios
		      .get( this.PokeApiURL + 'pokemon/' + this.pokemon + '/')
		      .then(response => (

		      		this.pokemonData.allInfo = response,
		      		this.pokemonData.speed = this.pokemonData.allInfo.data.stats[0].base_stat,
		      		this.pokemonData.specialDefense = this.pokemonData.allInfo.data.stats[1].base_stat,
		      		this.pokemonData.specialAttack = this.pokemonData.allInfo.data.stats[2].base_stat,
		      		this.pokemonData.defense = this.pokemonData.allInfo.data.stats[3].base_stat,
		      		this.pokemonData.attack = this.pokemonData.allInfo.data.stats[4].base_stat,
		      		this.pokemonData.hp = this.pokemonData.allInfo.data.stats[5].base_stat,
		      		this.pokemonData.id = this.pokemonData.allInfo.data.id

		      	));
		    //gets pokemon sprite
		    axios
		      .get( this.PokeApiURL + 'pokemon/' + this.pokemon + '/')
		      .then(response => (

		      		this.pokemonData.allInfo = response,
		      		this.pokemonData.speed = this.pokemonData.allInfo.data.stats[0].base_stat,
		      		this.pokemonData.specialDefense = this.pokemonData.allInfo.data.stats[1].base_stat,
		      		this.pokemonData.specialAttack = this.pokemonData.allInfo.data.stats[2].base_stat,
		      		this.pokemonData.defense = this.pokemonData.allInfo.data.stats[3].base_stat,
		      		this.pokemonData.attack = this.pokemonData.allInfo.data.stats[4].base_stat,
		      		this.pokemonData.hp = this.pokemonData.allInfo.data.stats[5].base_stat,
		      		this.pokemonData.id = this.pokemonData.allInfo.data.id

		      	));  
		},
		methods : {
			getPokemonSprites(pokedexNum) {
			    var images = require.context('../../assets/sprites', false, /\.png$/)
			    return images('./' + pokedexNum + ".png")
  }
		}		
	};
	
</script>


<style scoped>
	
	.pokemonCard{
		margin: 10px;
	}
	
	.pokemon{
		cursor: pointer;
	}

	.pokemon:hover{
		background-color: #ffebe6;
	}
</style>










